import './App.css';  // Ensure this import is presentimport React, { useState } from 'react';
import { CssBaseline } from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import Form from './Form.js';

const theme = createTheme({
  palette: {
    background: {
      default: '#f8f9fa',
    },
  },
});

function App() {
  
  return (
    <div>
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className="App">
        <Form />
      </div>
    </ThemeProvider>
    </div>
  );
}

export default App;
